export default Home

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            categories
            date(formatString: "MMMM DD, YYYY")
            title
            image {
              relativePath
              publicURL
            }
            images {
              publicURL
            }
          }
        }
      }
    }
  }
`
