import React from 'react'
import styled from 'styled-components'
import { get } from 'lodash/fp'

export const BaseButton = styled.div`
  background: ${get('theme.colors.lightYellow')};
  border: 2px solid ${get('theme.colors.lightYellow')};
  padding: 12px 20px 10px 20px;
  color: ${get('theme.colors.darkTeal')};
  cursor: pointer;
  transition: all 300ms ease;
  width: ${({ fullWidth }) => fullWidth ? '100%' : 'max-content'};
  text-align: center;
  font-family: 'Josefin Sans';
  font-size: 10px;
  text-transform: uppercase;
  display: flex;
  align-items: baseline;
  justify-content: center;
  &:hover {
    transition: all 300ms ease;
    background: white;
    border-color: white;
    color: ${get('theme.colors.darkTeal')};
  }
`
export const OutlineButton = BaseButton.extend`
  background: transparent;
  color: ${get('theme.colors.lightYellow')};
  &:hover {
    ${'' /* background: ${get('theme.colors.lightYellow')};
    border-color: ${get('theme.colors.lightYellow')};
    color: white; */}
    background: transparent;
    color: white;
    ${'' /* text-shadow: 0px 0px 10px rgba(0,0,0,0.4);
    box-shadow: 0px 0px 10px rgba(0,0,0,0.4); */}
  }
`
export const OutlineLinkAsButton = styled.a`
  background: transparent;
  border: 2px solid ${get('theme.colors.lightYellow')};
  padding: 12px 20px 10px 20px;
  color: ${get('theme.colors.lightYellow')};
  cursor: pointer;
  transition: all 300ms ease;
  width: ${({ fullWidth }) => fullWidth ? '100%' : 'max-content'};
  text-align: center;
  font-family: 'Josefin Sans';
  font-size: 10px;
  text-transform: uppercase;
  display: flex;
  align-items: baseline;
  justify-content: center;
  box-shadow: none;
  &:hover {
    transition: all 300ms ease;
    color: white;
    border-color: white;
  }
`
export const TealButton = BaseButton.extend`
  background: ${get('theme.colors.darkTeal')};
  border-color: ${get('theme.colors.darkTeal')};
  color: white;
  &:hover {
    background: ${get('theme.colors.lightTeal')};
    border-color: ${get('theme.colors.lightTeal')};
    color: white
  }
`
export const OutlineDark = TealButton.extend`
  background: transparent;
  color: ${get('theme.colors.darkTeal')};
  &:hover {
    background: transparent;
    color: ${get('theme.colors.lightTeal')};
  }
`
export default class ButtonComponent extends React.Component {
  render() {
    const { value, onClick, type, fullWidth } = this.props
    let ButtonType = BaseButton
    if(type === 'outline') ButtonType = OutlineButton
    if(type === 'teal') ButtonType = TealButton
    if(type === 'outlineDark') ButtonType = OutlineDark
    return (
      <ButtonType onClick={onClick} fullWidth={fullWidth}>{value}</ButtonType>
    )
  }
}
