import React from 'react'
import Button from '../button'
import ReactDOM from 'react-dom'
import { OutlineLinkAsButton } from '../button'
import { Container, Inner, WindowContainer, WindowDiv, SubTitle, ButtonContainer, ButtonWrapper } from './_ui'

export default class HeroSection extends React.Component {
  constructor(props) {
    super(props)
  }
  scrollToDomRef() {
    const domNode = ReactDOM.findDOMNode(this.props.reference.current)
    domNode.scrollIntoView({ block: 'start', behavior: 'smooth'})
  }
  render() {
    return (
      <Container>
        <Inner>
          <WindowContainer>
            <WindowDiv>
              <WindowDiv />
              <WindowDiv />
            </WindowDiv>
          </WindowContainer>
          <SubTitle>Welcome to 600sqft of family life in NYC</SubTitle>
          <ButtonContainer>
            <ButtonWrapper>
              <Button value="Start Reading" fullWidth onClick={() => this.scrollToDomRef()} />
            </ButtonWrapper>
            <ButtonWrapper>
              <OutlineLinkAsButton fullWidth href="mailto:hello@evolutionofastudio.com">
                Contact Us
              </OutlineLinkAsButton>
            </ButtonWrapper>
          </ButtonContainer>
        </Inner>
      </Container>
    )
  }
}
